\n    background: $purple;\n    border: none;\n    color: white;\n    font-weight: 500;\n    border-bottom: .3rem solid darken($purple, 10%);\n    padding: $s-size;\n}\n\n.button--link {\n    background: none;\n    border:none;\n    color:$off-white;\n    padding: 0;\n}\n\n@media(min-width:$desktop-breakpoint) {\n    .big-button {\n        margin-bottom: $xl-size;\n    }\n  }","//Widget\n\n.widget {\n    background: $light-blue;\n    margin-bottom: $l-size;\n}\n\n.widget--message {\n    color: $off-white;\n    margin:0;\n    padding: $l-size;\n    text-align: center;\n    border-bottom: 1px solid lighten($light-blue, 10%);\n}\n\n\n//Widget Header\n\n.widget-header {\n    background: $blue;\n    color: $off-white;\n    display: flex;\n    justify-content: space-between;\n    padding: $m-size;\n}\n\n.widget-header--title {\n    margin:0\n}",".option {\n    margin:0;\n    display: flex;\n    justify-content: space-between;\n    padding: $l-size $m-size;\n    border-bottom: 1px solid lighten($light-blue, 10%);\n}\n\n.option__text {\n    color:white;\n    font-weight: 500;\n    font-size: 2rem;\n    margin: 0;\n    word-break: break-all;\n}",".add-option-error {\n    font-style:italic;\n    color:$off-white;\n    padding: 0 $m-size;\n    margin: $m-size 0 0 0;\n}\n\n.add-option {\n    display: flex;\n    flex-direction: column;\n    padding: $m-size;\n}\n\n.add-option__input {\n    flex-grow: 1;\n    background: $dark-blue;\n    border: none;\n    border-bottom: .3rem solid darken($dark-blue, 10%);\n    color: $off-white;\n    padding: $s-size ;\n    //margin-right: $s-size;\n    margin: 0 0 $s-size 0\n}   \n\n@media (min-width: $desktop-breakpoint) {\n    .add-option {\n        flex-direction: row;\n    }\n\n    .add-option__input {\n        margin: 0 $s-size 0 0 \n    }\n}",".ReactModalPortal > div {\n    opacity: 0\n}\n\n.ReactModalPortal .ReactModal__Overlay {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    transition: opacity 200ms ease-in-out;\n}\n\n.ReactModalPortal .ReactModal__Overlay--after-open {\n    opacity: 1;\n}\n\n.ReactModalPortal .ReactModal__Overlay--before-close{\n    opacity: 0;\n}\n\n.modal {\n    background: $light-blue;\n    max-width: 30rem;\n    color: white;\n    outline: none;\n    padding: $l-size;\n    text-align: center;\n}\n\n.modal_title {\n    margin: 0 0 $m-size 0;\n}\n\n.modal__body {\n    font-size: 2rem;\n    font-weight: 300;\n    margin: 0 0 $l-size 0;\n    word-break: break-all;\n}"]}]);
